* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  color: #333;
}

.header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 100px 20px;
  text-align: center;
}

.header h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.header p {
  font-size: 1.5rem;
  opacity: 0.9;
}

section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  text-align: center;
}

.about p {
  font-size: 1.2rem;
  line-height: 1.8;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.project-card {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 30px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #667eea;
}

.project-card p {
  color: #666;
  line-height: 1.6;
  margin-bottom: 20px;
}

.links {
  display: flex;
  gap: 15px;
}

.links a {
  padding: 10px 20px;
  background: #667eea;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background 0.3s;
}

.links a:hover {
  background: #764ba2;
}

.contact {
  text-align: center;
  background: #f5f5f5;
  border-radius: 10px;
}

.contact a {
  color: #667eea;
  font-size: 1.2rem;
  text-decoration: none;
  margin-top: 20px;
  display: inline-block;
}

.contact a:hover {
  text-decoration: underline;
}